.steps__item {
  grid-template-columns: 40px 1fr;
  grid-template-areas: "number left";
  column-gap: 30px;
  display: grid;
}

.steps__item div:last-child {
  display: none;
}

.steps__step {
  background: #dac5a70d;
  border: 1px solid #dac5a726;
  border-radius: 2px;
  grid-area: left;
  padding: 24px;
  position: relative;
}

.steps__item--label {
  color: #dac5a7;
  font-family: var(--fontChillax);
  text-transform: uppercase;
  background: #dac5a70d;
  border: 1px solid #dac5a726;
  border-radius: 2px;
  grid-area: number;
  padding: 8px;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%;
  position: absolute;
  top: 10px;
  right: 10px;
}

.steps__item--text1 {
  color: #dac5a799;
  font-family: var(--fontSatoshi);
  letter-spacing: 1.5px;
  text-transform: uppercase;
  margin-bottom: 4px;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
}

.steps__item--text2 {
  color: #dac5a799;
  font-family: var(--fontChillax);
  margin-bottom: 32px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 180%;
}

.steps__item--title {
  color: #dac5a7;
  font-family: var(--fontSatoshi);
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 16px;
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: 130%;
}

.steps__step--list {
  color: #dac5a7;
  font-family: var(--fontChillax);
  flex-direction: column;
  gap: 16px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 180%;
  display: flex;
}

.steps__step--list li > svg {
  margin-right: 16px;
}

.steps__number-label {
  align-items: center;
  display: flex;
  position: relative;
}

.steps__number-label:before {
  content: "";
  background-color: #dac5a726;
  width: 1px;
  height: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.steps__item:last-child .steps__number-label:before {
  height: 50%;
  top: 0;
}

.steps__number-label span {
  z-index: 2;
  color: #dac5a7;
  width: 40px;
  height: 40px;
  font-family: var(--fontSatoshi);
  letter-spacing: 1.5px;
  text-transform: uppercase;
  background: #dac5a70d;
  border: 1px solid #dac5a726;
  border-radius: 500px;
  justify-content: center;
  padding: 8px 12px;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
  display: flex;
}

.maska {
  background: var(--bgColor);
  content: "";
  z-index: 1;
  width: 40px;
  height: 72px;
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

@media screen and (width >= 1080px) {
  .steps__step {
    padding: 48px;
  }

  .steps__item + .steps__item {
    margin-top: -48px;
  }

  .steps__item:nth-child(2n) {
    grid-template-areas: "left number right";
  }

  .steps__item {
    grid-template-columns: 1fr 40px 1fr;
    grid-template-areas: "right number left";
    column-gap: 80px;
  }

  .steps__item div:last-child {
    grid-area: right;
    display: block;
  }

  .steps__item:nth-child(2n) .steps__number-label:before {
    height: calc(100% - 96px);
  }

  .steps__item:first-child .steps__number-label:before {
    height: calc(100% + 96px);
    top: -96px;
  }

  .steps__item--label {
    top: 20px;
    right: 20px;
  }
}

@media screen and (width <= 1079px) {
  .steps__item + .steps__item .steps__step {
    margin-top: 20px;
  }

  .steps__item {
    padding-right: 20px;
  }
}

